import React from 'react'
import Container from 'react-bootstrap/esm/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Jumbo from 'react-bootstrap/Jumbotron'
import Image from 'react-bootstrap/Image'
import Button from 'react-bootstrap/Button'
import Calendario from './Calendario/Calendario'
import Jumbotron from 'react-bootstrap/Jumbotron'
import Form from 'react-bootstrap/Form'
import { useState } from 'react';
import { send } from 'emailjs-com';
import{ init } from 'emailjs-com';
init("user_HTKclytb8nQjhYLQQbKyu");
export default function Contacto() {
    const [m, setM] = useState("")
    const [toSend, setToSend] = useState({
        from_name: '',
        correo: '',
        cuarto:'',
        distrito:'',
        telefono:'',
        message:'',
        replayto:''
    });
    // const onSubmit = 
    const handleChange = (e) => {
        setToSend({ ...toSend, [e.target.name]: e.target.value });
    };
    return (
        <>
            <Jumbotron className="fondo-transparente" >
                <h1 style={{ textAlign: "center" }}>Contacto</h1>
                <h3 style={{ textAlign: "center" }} >Ponte en contacto con nosotros para recibir mas informacion o aclarar dudas</h3>
                <Container>

                    <Row>
                        <Col>
                            <Jumbotron className="fondo-transparente">
                                <h3 style={{ textAlign: "center" }} >Enviame directamente un correo</h3>
                                <Form.Control placeholder="Nombre" 
                                name='from_name'
                                 value={toSend.from_name}
                                 onChange={handleChange}
                                />
                                <br />
                                <Form.Control placeholder="Correo electronico" 
                                name='correo'
                                 value={toSend.correo}
                                 onChange={handleChange}
                                />
                                <br />
                                <Form.Control placeholder="Tipo de cuarto de interes" 
                                name='cuarto'
                                 value={toSend.cuarto}
                                 onChange={handleChange}
                                />
                                <br />
                                <Form.Control placeholder="Distrito" 
                                name='distrito'
                                 value={toSend.distrito}
                                 onChange={handleChange}
                                />
                                <br />
                                <Form.Control placeholder="Numero telefonico" 
                                name='telefono'
                                 value={toSend.telefono}
                                 onChange={handleChange}
                                />
                                <br />
                                <Form.Group controlId="exampleForm.ControlTextarea1">
                                    <Form.Control as="textarea" rows={3} placeholder="Mensaje..." 
                                    name='message'
                                    value={toSend.message}
                                    onChange={handleChange}
                                    />
                                </Form.Group>
                                <br />
                                <Button style={{ float: "right" }} ><span style={{ margin: "auto 1rem" }} onClick={
                                            () => {
                                                send(
                                                  'service_rdyl58z',
                                                  'template_wa6o2iq',
                                                  toSend
                                                )
                                                  .then((response) => {
                                                    console.log('SUCCESS!', response.status, response.text);
                                                    
                                                    setToSend({
                                                        from_name: '',
                                                        correo: '',
                                                        cuarto:'',
                                                        distrito:'',
                                                        telefono:'',
                                                        message:'',
                                                        replayto:''
                                                    })
                                                    setM("Gracias! nos estaremos comunicando pronto contigo!")
                                                  })
                                                  .catch((err) => {
                                                    console.log('FAILED...', err);
                                                    setToSend({
                                                        from_name: '',
                                                        correo: '',
                                                        cuarto:'',
                                                        distrito:'',
                                                        telefono:'',
                                                        message:'',
                                                        replayto:''
                                                    })
                                                    setM("Parece que nuestro servicio de mensajeria no esta funcionando intenta de nuevo mas tarde!")
                                                  });
                                              }
                                        }>Enviar</span></Button>
                                         <h4>{m}</h4>
                            </Jumbotron>
                        </Col>
                        <Col>
                            <Jumbotron className="fondo-transparente">
                                <h1>
                                    Whatsapeame al telefono
                                </h1>
                                <h1> +51 943 047 804 </h1>
                                <h1>y te atenderé lo mas pronto posible :D</h1>
                            </Jumbotron>
                        </Col>
                    </Row>
                </Container>
            </Jumbotron>
        </>
    )
}
